
import { defineComponent } from "vue";
import { Canvas, Rect, Circle } from "@/modules/MetaIndex";

export default defineComponent({
  name: "basicShapeView",
  data() {
    return {
      rect: new Rect(),
      circle: new Circle(),
      Title: "绘制基本形状",
      contentTitle: "绘制图形步骤：",
      contentArray: [
        "第一步：创建画布 Canvas 实例 - let canvas = new Canvas()。",
        "第二步：创建矩形 Rect 实例和圆形 Circle 实例 - let rect = new Rect(); let rect = new Rect()。",
        "第三步：通过画布上的 add 方法把图形 rect 和 circle 添加到画布 Canvas 上 - canvas.add([rect, circle])。",
      ],
      describeTitle: "属性描述说明：",
      describeArray: [
        "Canvas 实例: 参数是一个对象类型，可以传入 width (类型 number)、height (类型 number) 去设置画布的大小，默认值 width 为 800, height 为 600。add 方法传入的参数类型是一个数组，数组里的类型为图形的实例对象。",
        "Rect 实例：参数是一个对象类型，可以传入 left (类型 number)、top (类型 number) 去设置矩形的摆放位置, width (类型 number)、height (类型 number) 去设置矩形的大小, stroke (类型 string)、fill (类型 string) 去设置矩形描边与填充的颜色，剩下属性可以自行去了解学习。",
        "Circle 实例：参数是一个对象类型，可以传入 left (类型 number)、top (类型 number) 去设置圆形的摆放位置, radius (类型 number) 去设置圆形的大小, stroke (类型 string)、fill (类型 string) 去设置圆形描边与填充的颜色，剩下属性可以自行去了解学习。",
      ],
      rectAttrTitle: "矩形获取或设置属性值：",
      rectFormData: [
        {
          title: "left",
          value: 0,
        },
        {
          title: "top",
          value: 0,
        },
        {
          title: "width",
          value: 0,
        },
        {
          title: "height",
          value: 0,
        },
        {
          title: "lineWidth",
          value: 1,
        },
        {
          title: "angle",
          value: 0,
        },
        {
          title: "scaleWidth",
          value: 1,
        },
        {
          title: "scaleHeight",
          value: 1,
        },
      ],
      circleAttrTitle: "圆形获取或设置属性值：",
      circleFormData: [
        {
          title: "left",
          value: 0,
        },
        {
          title: "top",
          value: 0,
        },
        {
          title: "radius",
          value: 0,
        },
        {
          title: "lineWidth",
          value: 1,
        },
        {
          title: "angle",
          value: 0,
        },
        {
          title: "scaleWidth",
          value: 1,
        },
        {
          title: "scaleHeight",
          value: 1,
        },
      ],
    };
  },
  // eslint-disable-next-line prettier/prettier
  methods: {
    drawShape: function () {
      let canvas = new Canvas();
      let rect = new Rect({
        left: 50,
        top: 50,
        width: 50,
        height: 50,
        stroke: "red",
      });
      let circle = new Circle({
        left: 800,
        top: 80,
        radius: 30,
        fill: "green",
      });
      canvas.add([rect, circle]);
      this.rect = rect;
      this.circle = circle;
    },
    rectSubmit() {
      this.rectFormData.map((item) => {
        if (item.value) {
          {
            this.rect.set({
              [item.title]: item.value,
            });
          }
        } else {
          if (
            item.title == "lineWidth" ||
            item.title == "scaleWidth" ||
            item.title == "scaleHeight"
          ) {
            item.value = this.rect.get(item.title);
          } else {
            this.rect.set({
              [item.title]: item.value,
            });
          }
        }
        console.log(this.rect.get(item.title));
      });

      this.rect.canvas.renderAll();
    },
    circleSubmit() {
      this.circleFormData.map((item) => {
        if (item.value) {
          {
            this.circle.set({
              [item.title]: item.value,
            });
          }
        } else {
          if (
            item.title == "lineWidth" ||
            item.title == "scaleWidth" ||
            item.title == "scaleHeight"
          ) {
            item.value = this.circle.get(item.title);
          } else {
            this.circle.set({
              [item.title]: item.value,
            });
          }
        }
        console.log(this.circle.get(item.title));
      });

      this.circle.canvas.renderAll();
    },
  },
  created() {
    this.drawShape();
  },
});
